import React from "react"
import {
  Button,
  ButtonArea
} from "react-weui"
import _ from "lodash";
import ButtonArticle from './button_arti';
const DATA= {
  info1:[
    {
      title:"1.	",
      content:"<p></p><br/><p></p><br/><p></p>"
    }
  ],
  info2:[
    {
      title:"2.	",
      content:"<p></p><ul}><li></li><li></li><li></li></ul>"
    }
  ]
}
class ButtonPage extends React.Component {
  state = {
    page: 0
  }

  render() {
    var article = _.map(DATA['info'+this.state.page], (info)=> {
      return <ButtonArticle onBtnClick={e=>this.setState({page:0})} display={this.state.page != 0 ? null : 'none'} _page={this.state.page} title={info.title} content={info.content}/>
    })
    return (
        <div style={{display: this.props.display}}>
          <div style={{display: this.state.page == 0 ? null : 'none'}}>
            <ButtonArea >
              <Button type="default" onClick={e=>this.setState({page:1})}>
                <p className="btn-page" style={{fontSize:'17px'}}>
                  1. 
                </p>

              </Button>

              <Button type="default" onClick={e=>this.setState({page:2})}>
              <p className="btn-page" style={{fontSize:'17px'}}>
                2. 
              </p>
              </Button>
              {/*<Button type="default" onClick={e=>this.setState({page:3})}>
                03. Wtf Am I
              </Button>*/}
            </ButtonArea>
          </div>
          {article}
          {/*
          <ButtonArticle onBtnClick={e=>this.setState({page:0})} display={this.state.page == 2 ? null : 'none'} _page="2"/>
          <ButtonArticle onBtnClick={e=>this.setState({page:0})} display={this.state.page == 3 ? null : 'none'} _page="3"/>
          */}
        </div>
    )
  }
}

export default ButtonPage;